<script>
export default {
  methods: {
    onScroll () {
      var navbar = document.getElementById('myNavbar')
      if (document.body.scrollTop > 75 || document.documentElement.scrollTop > 75) {
        if(navbar.className.includes('w3-hide')) {
          navbar.className = navbar.className.replace('w3-hide', '')
        }
        if(!navbar.className.includes('w3-animate-top')) {
          navbar.className += ' w3-animate-top'
        }
      } else {
        if(!navbar.className.includes('w3-hide')) {
          navbar.className += ' w3-hide'
        }
      }
    }
  },
  beforeDestroy () {
    window.onscroll = null
  },
  mounted () {
    window.onscroll = this.onScroll
  }
}
